.App {
  text-align: center;
  background: rgb(0, 0, 0);
  background: linear-gradient(
    90deg,
    rgba(0, 0, 0, 1) 0%,
    rgba(0, 30, 38, 1) 53%,
    rgba(33, 8, 8, 1) 100%
  );
  height: 100%;
  margin: 0;
  padding: 0;
  min-height: 100vh;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}
